import React, { Component, Fragment } from 'react'
import { Table, Form, Input, Button, AutoComplete,Modal,DatePicker,Select } from 'antd'
import { generateStyle, debounce } from '../util'
import { postAction } from '@/axios';
import MyModal from './modal'
import moment from 'moment'
const FormItem = Form.Item, AutoOption = AutoComplete.Option,Option=Select.Option
class BaseTable extends Component {
    state = {
        columns: [],
        dataSource: [],
        BindDataValue:null,
        ass_params:[],
        params:[],
        historyOptions:[],
        currentCell:'',
        visible:false,
        textareaValue:'',
        currentKey:'',
        currentItem:null,
    }
    getAssociation = (item,value) => {
        if(value !== ''){
            let params = this.fun_combineParam(item,'params',value)
            let param = {
                "condition": {
                    "tableWidgetId": this.props.item.common.onlyId,
                    "widgetId": item.common.onlyId,
                    "widgetValue": value
                },
                "params": params
            }
            postAction(`/ybt/form/task/association/data?formTaskId=${this.props.id}`, param).then(res => {
                if (res.success) {
                    this.props.handleUpdateOption(res.obj, this.props.item.id, 'associationOptions',item.id)
                }
            })
        }        
    }
    /**
    |--------------------------------------------------
    | 根据params拿到想要的参数
    |--------------------------------------------------
    */
    fun_combineParam = (current,type,value) => {
        let params = this.state[type] || []
        // let params=['NBGLTC.XM.BindDataValue']
        let list = this.props.list, obj = {}
        params.forEach(item => {
            if (item.indexOf('.') > 0) {
                // 参数关联的是表格列或者是值字段
                if(item.indexOf('BindDataValue')>=0){
                    // 存在关联某联想控件的情况
                    let beforeStr=item.replace('.BindDataValue','')
                    if(beforeStr.indexOf('.')>0){
                        // 表格列
                        let tableId=beforeStr.split('.')[0],childId=beforeStr.split('.')[1]
                        let tableItem=list.find(inner=>inner.common.onlyId === tableId)
                        if(tableItem){
                            let childIndex=tableItem.children.findIndex(i=>i.common.onlyId === childId)
                            if(childIndex>=0){
                                if(this.state.BindDataValue){
                                    obj[item]=this.state.BindDataValue[tableId][childId]
                                }else{
                                    obj[item]=''
                                }
                            }
                        }
                    }else{
                        // 其他
                        let index = list.findIndex(inner => inner.common.onlyId === beforeStr)
                        if (index >= 0) {
                            obj[item] = list[index].data.BindDataValue || ''
                        }
                    }
                }else{
                    let tableId=item.split('.')[0],childId=item.split('.')[1]
                    let tableItem=list.find(inner=>inner.common.onlyId === tableId)
                    if(tableItem){
                        let childIndex=tableItem.children.findIndex(i=>i.common.onlyId === childId)
                        if(childIndex>=0){
                            obj[item]=value
                        }
                    }
                }
            } else {
                let index = list.findIndex(inner => inner.common.onlyId === item)
                if (index >= 0) {
                    obj[item] = list[index].actualValue
                }
            }
        })
        return obj
    }
    handleCloseModal=()=>{
        this.setState({
            visible:false,
            textareaValue:''
        })
    }  
    handleClick=(item,key)=>{
         // 判断是否是多行文本框类型
        if(item.common.rowType === 'textarea'){
            this.setState({
                visible:true,
                textareaValue:this.props.form.getFieldValue(key),
                currentKey:key,
                currentItem:item
            })
            
        }
    }
    handleSaveShuju=(value)=>{
        this.setState({
            visible:false,
            textareaValue:''
        },()=>{            
            this.props.form.setFieldsValue({[this.state.currentKey]:value})        
            // 此处要触发关联填充
            // 检查关联
            setTimeout(()=>{
                let hasAss = this.fun_checkAssociattion(this.props.item, this.state.currentItem)
                if (hasAss) {
                    this.getBindAssociation(this.state.currentItem,value)
                }
            },0)
            
        })
    }
    /**
        |--------------------------------------------------
        | 获取焦点的时候去拿到当前组件联想或者关联的时候所需要的参数
        |--------------------------------------------------
        */
    handleFocus = (item) => {
        this.setState({
            currentCell:`${this.props.item.common.onlyId}.${item.common.onlyId}`
        })
        if (item.data.FillingMode === 'Association') {
            let param = {
                tableWidgetId:this.props.item.common.onlyId,
                widgetId: item.common.onlyId,
                formTaskId: this.props.id,
                fillingMode: 'Association'
            }
            postAction(`/ybt/form/task/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.setState({
                            params:res.obj
                        })
                    }
                }
            })
        }
        // 检查关联
        let hasAss = this.fun_checkAssociattion(this.props.item, item)
        if (hasAss) {
            let param = {
                tableWidgetId: this.props.item.common.onlyId,
                widgetId: item.common.onlyId,
                formTaskId: this.props.id,
                fillingMode: 'AssociationFilling'
            }
            postAction(`/ybt/form/task/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.setState({
                            ass_params:res.obj
                        })
                    }
                }
            })
        }
    }
    getHistory = (item,value) => {
        let param = {
            tableWidgetId: this.props.item.common.onlyId,
            widgetId: item.common.onlyId,
            formTaskId: this.props.id,
            condition: value
        }
        postAction(`/ybt/form/task/history/data`, param, 1, 1).then(res => {
            if (res.success) {
                this.props.handleUpdateOption(res.obj, this.props.item.id, 'historyOptions',item.id)
            }
        })
    }
    handleSearch = (item, value) => {
        let type = item.data.FillingMode
        if (type === 'Manual') {
            this.getHistory(item,value)
        } else if (type === 'Association') {
            this.getAssociation(item,value)
        }
    }
    // 得到关联填充的数据
    getBindAssociation = (item,value,index) => {
        let params = this.fun_combineParam(item,'ass_params',value)
        postAction(`/ybt/form/task/associationFill/data?formTaskId=${this.props.id}&tableWidgetId=${this.props.item.common.onlyId}&widgetId=${item.common.onlyId}`, params).then(res => {
           
            // 推送返回的关联填充的数据            
            if (res.success) {
                // 将取到的数据推送到父级的list   
                // 如果当前单元是date类型，先转化为字符串
                if(item.common.rowType === 'date'){
                    value=moment(value).format('YYYY-MM-DD')
                }    
                this.props.fillRow(this.props.item.id,item.common.onlyId,value,res.obj,index)
            }
        })
    }
    // 检首先要过滤掉自身，表格组件只能被当前表格内的组件关联
    fun_checkAssociattion = (parent, current) => {
        let tag = false, list = parent.children
        list = list.filter(item => item.id !== current.id)
        list.forEach(item => {            
            if (item.data && item.data.AssociatedControlsID === current.common.onlyId) {
                tag = true
            }
        })
        return tag
    }
    handleSelect = (obj,value, option) => {    
        // 为了保证获取数据的及时性，将值字段的取值存到当前组件的state里面    
        if(obj.data.FillingMode === 'Association'){
            let index = Number(option.key)
            let item = obj.common.associationOptions[index]
            let val = item[obj.data.BindDataField] || ''
            if(this.state.BindDataValue){
                let tmp=JSON.parse(JSON.stringify(this.state.BindDataValue))
                if(tmp[this.props.item.common.onlyId]){
                    tmp[this.props.item.common.onlyId]={
                        ...tmp[this.props.item.common.onlyId],
                        [obj.common.onlyId]:val
                    }
                }else{
                    tmp[this.props.item.common.onlyId]={
                        [obj.common.onlyId]:val
                    }
                }
                this.setState({
                    BindDataValue:tmp
                })
            }else{
                this.setState({
                    BindDataValue:{
                        [this.props.item.common.onlyId]:{
                            [obj.common.onlyId]:val
                        }
                    }
                })
            }
        }        
    }
   
    handleChange = (item,paramName,index,value) => {
        
        // 检查关联
        let hasAss = this.fun_checkAssociattion(this.props.item, item)
        if (hasAss) {
            this.getBindAssociation(item,value,index)
        }
    }
    handleAddRow = () => {
        let obj = {}
        this.props.item.children.forEach(item => {
            if (item.common.onlyId !== '') {
                obj[item.common.onlyId] = ''
            }
        })
        this.props.handleAddRow(obj, this.props.item.id)
    }
    componentDidMount() {
        this.combineTable(this.props);
        
    }    
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps.item) !== JSON.stringify(this.props.item)) {
            this.props.form.resetFields() 
            this.combineTable(this.props)
        }
    }
   
    handleDeleteRow = (record) => {
        Modal.confirm({
            title:'删除',
            content:'确定删除吗',
            cancelText:'取消',
            okText:'确定',
            onOk:()=>{
                this.props.handleDeleteRow(record, this.props.item.id)
            }
        })
    }
    combineTable = (props) => {
        let { getFieldDecorator } = props.form
        let columns = props.item.children.map((item, index) => {
            let historyOptions = item.common.historyOptions || []
            let associationOptions = item.common.associationOptions || []
            /**
            |--------------------------------------------------
            | 根据options生成统一的下拉选项
            |--------------------------------------------------
            */
            let useOptions = []
            if (item.data.FillingMode === 'Manual') {
                useOptions = historyOptions.map(inner => {
                    let newObj = {}
                    // 过滤掉时间戳
                    if (inner.submitTime) {
                        delete inner.submitTime
                    }
                    // 遍历item
                    for (let k in inner) {
                        if (k && inner[k] && inner[k] !== '') {
                            newObj.name = inner[k]
                            newObj.value = inner[k]
                        }
                    }
                    return newObj
                })
            } else if (item.data.FillingMode === 'Association') {

                // 联想模式
                useOptions = associationOptions.map(inner => {
                    let newObj = {}, str = []
                    newObj.value = (inner[item.data.BindDataField]||'')+','+inner[item.data.DataValueField] || ''
                    for (let key in inner) {
                        str.push(inner[key])
                    }
                    newObj.name = str.join('，')
                    return newObj
                })
            }
            // 过滤options防止报错
            useOptions = useOptions.filter(item => item.name && item.name !== '')
            let tips=''
            if(item.data.FillingMode === 'Association'){
                tips='请选择联想下拉框中的项'
            }
            let width=150
            if(item.common && item.common.width){
                width=item.common.width
            }
            if(item.common.rowType === 'date'){
                // 日期组件
                return {
                    title: item.common.title,
                    dataIndex: item.common.onlyId,
                    width,
                    render: (text, record,index) => {
                        return <FormItem>
                            {
                                getFieldDecorator(`${item.common.onlyId}##${record.key}`, {
                                    initialValue: !text || text === '' ? null : moment(text)
                                })(<DatePicker
                                    onChange={debounce(this.handleChange.bind(this,item,`${item.common.onlyId}##${record.key}`,index),800,this)}
                                    onFocus={this.handleFocus.bind(this,item)}
                                />)
                            }
                        </FormItem>
                    }
                }
            }else if(item.common.rowType === 'select'){
                let options=[]
                // console.log(this.state.enumerationData)
                if(this.props.enumerationData && this.props.enumerationData[this.props.item.common.onlyId] && this.props.enumerationData[this.props.item.common.onlyId][item.common.onlyId]){
                    options=this.props.enumerationData[this.props.item.common.onlyId][item.common.onlyId]
                }else{
                    if(item.common.options && item.common.options !== ''){
                        options=item.common.options.split(/,|，/)
                        options=options.map(item=>{
                            if(item && item !== ''){
                                return {
                                    key:item,
                                    value:item
                                }
                            }else{
                                return  null
                            }
                        }).filter(item=>item)
                    }
                }
                // 下拉框组件
                return {
                    title: item.common.title,
                    dataIndex: item.common.onlyId,
                    width,
                    render: (text, record,index) => {
                        return <FormItem>
                            {
                                getFieldDecorator(`${item.common.onlyId}##${record.key}`, {
                                    initialValue: text ? text : ''
                                })(<Select 
                                    onChange={debounce(this.handleChange.bind(this,item,`${item.common.onlyId}##${record.key}`,index),800,this)}  
                                    filterOption={(input, option) =>option.props.children.indexOf(input) >= 0}
                                    showSearch
                                    optionFilterProp="children"
                                    onFocus={this.handleFocus.bind(this,item)}
                                >
                                {
                                    options.map((item,index)=><Option value={item.value} key={index}>{item.key}</Option>)
                                }
                            </Select>)
                            }
                        </FormItem>
                    }
                }
            }else{
                return {
                    title: item.common.title,
                    dataIndex: item.common.onlyId,
                    width,
                    render: (text, record,index) => {
                        return <FormItem>
                            {
                                getFieldDecorator(`${item.common.onlyId}##${record.key}`, {
                                    initialValue: text ? text : ''
                                })(<AutoComplete
                                    onFocus={this.handleFocus.bind(this,item)}
                                    onSearch={debounce(this.handleSearch.bind(this,item),800,this)}
                                    optionLabelProp='value'
                                    onSelect={this.handleSelect.bind(this,item)}
                                    onChange={debounce(this.handleChange.bind(this,item,`${item.common.onlyId}##${record.key}`,index),800,this)}
                                    dataSource={useOptions.map((item, index) => <AutoOption key={index} value={item.value}>{item.name}</AutoOption>)}
                                ><Input 
                                    onClick={this.handleClick.bind(this,item,`${item.common.onlyId}##${record.key}`)} 
                                    placeholder={tips} 
                                    readOnly={props.item.common.ReadOnly ? true : false}/></AutoComplete>)
                            }
                        </FormItem>
                    }
                }
            }
        })
        if (props.item.common.SerialNumber) {
            columns.unshift({
                title: '序号',
                dataIndex: 'key',
                width:80
            })
        }
        //如果只读模式 没有删除操作列
        if(!props.item.common.ReadOnly){
            columns.push({
                title: '操作',
                dataIndex: 'action',
                align: 'center',
                width:150,
                render: (text, record) => {
                    return <Fragment>
                        <span onClick={() => { this.handleDeleteRow(record) }} style={{ cursor: 'pointer', marginRight: 15, color: 'red' }}>删除</span>
                    </Fragment>
                }
            })
        }
        let dataSource = props.item.actualValue? props.item.actualValue : []
        dataSource = dataSource.filter(item => item).map((item, index) => {
            item.key = index + 1
            return item
        })
        this.setState({
            columns,
            dataSource
        })
    }
    render() {
        let tableWidth=0
        let { columns, dataSource,visible,textareaValue  } = this.state
        columns.forEach(item=>{            
            tableWidth +=Number(item.width)            
        })
        let style = generateStyle(this.props.item.style) || {}
        return (
            <Form style={{width:'100%',overflowY:'hidden',overflowX:'auto'}}>
                <Table
                    columns={columns}
                    style={{...style,
                        width:`${tableWidth}px`,minWidth:950
                    }}
                    dataSource={dataSource}
                    title={() => {
                        return <div style={{ display: 'flex', alignItems: 'center' }}><span>
                            {this.props.item.common.Caption}
                        </span>
                            {
                                this.props.item.common.ReadOnly?null:<Button style={{marginLeft:'10px'}} onClick={this.handleAddRow} icon='plus' type='primary'>新增行</Button>
                            }
                        </div>
                    }}
                    bordered
                    size={'small'}
                    pagination={false}
                >

                </Table>
                {
                    visible && <MyModal 
                                    visible={visible}
                                    value={textareaValue}
                                    item={this.props.item}
                                    params={this.state.params}
                                    list={this.props.list}
                                    id={this.props.id}
                                    childitem={this.state.currentItem}
                                    handleClose={this.handleCloseModal}
                                    handleSaveShuju={this.handleSaveShuju}
                                />
                }
            </Form>

        )
    }
}


export default Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        props.handleEditTable(allFields, props.item.id, changedFields)
    }
})(BaseTable)