import React, { Component } from 'react'
import {Form,Upload,Button,Icon} from 'antd'
import {generateStyle} from '../util'
const FormItem=Form.Item
class BaseRadio extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        let item=this.props.item
        const formItemLayout = {
            labelCol: {
              span:4
            },
            wrapperCol: {
              span:20
            },
        };
        let style=generateStyle(this.props.item.style) || {}
        return (
            <Form {...formItemLayout}>
                <FormItem label={item.common.label}>
                    {
                        getFieldDecorator('input',{

                        })(<Upload style={style} disabled>
                            <Button>
                                <Icon type="upload" /> Click to Upload
                            </Button>
                        </Upload>)
                    }
                </FormItem>
            </Form>
        )
    }
}
BaseRadio=Form.create()(BaseRadio)
export default BaseRadio
