import React, { Component } from 'react'
import { Form, Input, AutoComplete } from 'antd'
import { generateStyle, debounce } from '../util'
import { postAction } from '@/axios';
import './index.less'
const FormItem = Form.Item, { TextArea } = Input, AutoOption = AutoComplete.Option
/**
 *解决浮点数计算的精度丢失问题0.2+0.1=0.30000001
 *
 * @param {*} arg1
 * @param {*} arg2
 * @returns
 */
const accAdd=(arg1, arg2) => {
    var r1, r2, m;
    try {
        r1 = arg1.toString().split(".")[1].length;
    } catch(e) {
        r1 = 0;
    }
    try {
        r2 = arg2.toString().split(".")[1].length;
    }catch(e){
        r2 = 0;
    }
    m = Math.pow(10, Math.max(r1, r2));
    let sum = (arg1 * m + arg2 * m) / m
    return isNaN(sum)?'':sum;
}  

/**
 *  注意，由于不同组件之间的下拉选项需要保持独立性，所以每次请求的下拉需要保存到list中！！！
 * @class BaseInput
 * @extends {Component}
 */
class BaseInput extends Component {    
    /**
     *联想模式下拉选项
     * @memberof BaseInput
     */
    getAssociation = (value) => {
        if(value !== ''){
            let params=this.fun_combineParam(this.props.item.common['params'] || [])
            let param = {
                "condition": {
                    "tableWidgetId": "",
                    "widgetId": this.props.item.common.onlyId,
                    "widgetValue": value
                },
                "params": params
            }
            postAction(`/ybt/form/task/association/data?formTaskId=${this.props.id}`, param).then(res => {
                if (res.success) {
                    this.props.handleUpdateOption(res.obj,this.props.item.id,'associationOptions')                
                }
            })
        }                
    }
    
    /**
     *根据params拿到想要的参数     *
     * @memberof BaseInput
     */
    fun_combineParam=(params)=>{
        let list=this.props.list,obj={}
        params.forEach(item=>{
            if(item.indexOf('.')>0){
                // 参数关联的是表格列或者是值字段
                if(item.indexOf('BindDataValue')>=0){
                    let index = list.findIndex(inner => inner.common.onlyId ===  item.split('.')[0])
                    if (index >= 0) {
                        obj[item] = list[index].data.BindDataValue || ''
                    }
                }
            }else{
                let index=list.findIndex(inner=>inner.common.onlyId === item)
                if(index>=0){
                    obj[item]=list[index].actualValue
                }                
            }
        })
        return obj
    }
    
    /**
     *触发focus事件
     *
     * @memberof BaseInput
     */
    handleFocus = () => {
        if (this.props.item.data.FillingMode === 'Association') {
            let param = {
                tableWidgetId: '',
                widgetId: this.props.item.common.onlyId,
                formTaskId: this.props.id,
                fillingMode: 'Association'
            }
            postAction(`/ybt/form/task/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.props.handleUpdateOption(res.obj,this.props.item.id,'params')                        
                    }
                }
            })
        }
        // 检查关联
        let hasAss=this.fun_checkAssociattion(this.props.item.common.onlyId)
        if(hasAss){
            let param = {
                tableWidgetId: '',
                widgetId: this.props.item.common.onlyId,
                formTaskId: this.props.id,
                fillingMode: 'AssociationFilling'
            }
            postAction(`/ybt/form/task/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.props.handleUpdateOption(res.obj,this.props.item.id,'ass_params')                        
                    }
                }
            })
        }
    }

    /**
     *历史填充记录查询
     *
     * @memberof BaseInput
     */
    getHistory = (value) => {
        let param = {
            tableWidgetId: '',
            widgetId: this.props.item.common.onlyId,
            formTaskId: this.props.id,
            condition: value
        }
        postAction(`/ybt/form/task/history/data`, param, 1, 1).then(res => {
            if (res.success) {
                this.props.handleUpdateOption(res.obj,this.props.item.id,'historyOptions')               
            }
        })
    }
    /**
     *处理search事件，主要针对历史记录和联想模式
     *
     * @memberof BaseInput
     */
    handleSearch = (value) => {
        let type = this.props.item.data.FillingMode
        if (type === 'Manual') {
            this.getHistory(value)
        } else if (type === 'Association') {
            this.getAssociation(value)
        }        
    }
    /**
     *初次关联填充的请求
     *
     * @memberof BaseInput
     */
    getBindAssociation=()=>{
        let params=this.fun_combineParam(this.props.item.common['ass_params'] || [])
        postAction(`/ybt/form/task/associationFill/data?formTaskId=${this.props.id}&tableWidgetId=&widgetId=${this.props.item.common.onlyId}`, params).then(res => {
            if (res.success) {
                // 将取到的数据推送到父级的list
                this.props.sendAssociationData(res.obj)
                if(res.obj){
                    // 递归调用链式关联
                    this.chainAssociattion(res.obj)                    
                }
            }
        })
    }
    /**
     *递归调用关联填充的全部数据并回显
     *
     * @memberof BaseInput
     */
    chainAssociattion=(data)=>{
        for(let k in data){
            if(k){
                let isAss=this.fun_checkAssociattion(k)
                if(isAss){
                    // 首先拿参数
                    let param = {
                        tableWidgetId: '',
                        widgetId: k,
                        formTaskId: this.props.id,
                        fillingMode: 'AssociationFilling'
                    }
                    postAction(`/ybt/form/task/need/widget/dataParams`, param, 1, 1).then(res => {
                        if (res.success) {
                            let params=this.fun_combineParam(res.obj)
                            postAction(`/ybt/form/task/associationFill/data?formTaskId=${this.props.id}&tableWidgetId=&widgetId=${k}`, params).then(res => {
                                if (res.success) {
                                    this.props.sendAssociationData(res.obj)
                                    if(res.obj){
                                        this.chainAssociattion(res.obj)                   
                                    }
                                }
                            })  
                        }
                    })                                      
                }
            }
        }
    }
    /**
     *检测是否有其他任意组件和当前组件关联,首先要过滤掉自身
     *
     * @memberof BaseInput
     */
    fun_checkAssociattion=(onlyId)=>{
        let tag=false,list=this.props.list
        list=list.filter(item=>item.common.onlyId !== onlyId)
        list.forEach(item=>{
            if(item.children){
                if(item.children.length>0){
                    let index=item.children.findIndex(i=>onlyId === i.data.AssociatedControlsID)
                    if(index>=0){
                        tag=true
                    }
                }
            }
            if(item.data && onlyId === item.data.AssociatedControlsID){
                tag=true
            }
        })
        return tag
    }
   
    handleChange=(value,option)=>{
        if(this.props.item.data.FillingMode === 'Association'){
            let index=Number(option.key)        
            let item=this.props.item.common.associationOptions[index]
            let val=item[this.props.item.data.BindDataField]||''
            this.props.form.setFieldsValue({
                'actualValue':value.split(',')[1]
            })
            this.props.sendBindDataValue(this.props.item.id,val)
        }
        // 检查关联
        let hasAss=this.fun_checkAssociattion(this.props.item.common.onlyId)
        if(hasAss){
            this.getBindAssociation()
        }
    }
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps) !== JSON.stringify(this.props)) {
            // 重置所有表单项 ，方便更新数据
            this.props.form.resetFields()
        }
    }
/**
     *计算公式的值
     * @memberof BaseInput
     */
    calculate=(item,list)=>{
        if(item.data.formula && item.data.formula !== ''){
            let onlyId_list=item.data.formula.split('+')
            // 检查是否存在公式嵌套
            let total=onlyId_list.reduce((prev,current)=>{
                // 查找公式中配置的onlyid在不在list中
                // 区分两种情况，普通组件和table组件
                let number=0
                if(current.indexOf('.')>0){
                    // table
                    let tableId=current.split('.')[0],rowId=current.split('.')[1]
                    // 先找到table
                    let tableData=list.find(inner=>inner.common.onlyId === tableId)
                    if(tableData){
                        let tableDataActualValue = tableData.actualValue || [];
                        number=tableDataActualValue.reduce((prev,current)=>{
                            return accAdd(prev,Number(current[rowId]))
                        },0)
                    }
                }else{
                    // 其他组件
                    let target=list.find(inner=>inner.common.onlyId === current)
                    if(target){
                        number=Number(target.actualValue)
                    }
                }
                return accAdd(prev,number)
            },0)
            return total
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        // const formItemLayout = {
        //     labelCol: {
        //         span: 4
        //     },
        //     wrapperCol: {
        //         span: 20
        //     },
        // }
        let item = this.props.item, style = generateStyle(item.style) || {}
        let historyOptions=item.common.historyOptions || []
        let associationOptions=item.common.associationOptions || []
        let calculateTag=!item.data.formula || item.data.formula === ''        

        /**
        |--------------------------------------------------
        | 根据options生成统一的下拉选项
        |--------------------------------------------------
        */
        let useOptions = []
        if (item.data.FillingMode === 'Manual') {
            useOptions = historyOptions.map(inner => {
                let newObj = {}
                // 过滤掉时间戳
                if (inner.submitTime) {
                    delete inner.submitTime
                }
                // 遍历item
                for (let k in inner) {
                    if (k && inner[k] && inner[k] !== '') {
                        newObj.name = inner[k]
                        newObj.value = inner[k]
                    }
                }
                return newObj
            })            
        } else if (item.data.FillingMode === 'Association') {
            // 联想模式
            useOptions = associationOptions.map(inner => {
                let newObj = {}, str = []
                // 此处为了解决下拉框选项重复的问题，将值字段拼接进去作为区分
                newObj.value = (inner[item.data.BindDataField]||'')+','+inner[item.data.DataValueField]||''
                for (let key in inner) {
                    str.push(inner[key])
                }
                newObj.name = str.join('，')
                return newObj
            })
        }
        // 过滤options防止报错
        useOptions = useOptions.filter(item => item.name && item.name !== '')
        let tips=''
        if(item.data.FillingMode === 'Association'){
            tips='请选择联想下拉框中的项'
        }
        // 统一返回自动完成组件
        return (
            <Form 
                // {...formItemLayout}
            >
                {
                    item.common.TextMode === 'SingleLine' ?
                        <FormItem labelCol={{span:4}} wrapperCol={{span:20}} label={item.common.label}>
                            {
                                getFieldDecorator('actualValue', {
                                    initialValue: calculateTag?item.actualValue:this.calculate(item,this.props.list),
                                    rules:[
                                        {
                                            required: item.common.MustFill,
                                        }
                                    ]
                                })(<AutoComplete
                                    onFocus={this.handleFocus}
                                    onSearch={debounce(this.handleSearch, 800, this)}
                                    onChange={debounce(this.handleChange, 800, this)}
                                    optionLabelProp='value'
                                    disabled={!calculateTag}
                                    dataSource={useOptions.map((item, index) => <AutoOption key={index} value={item.value}>{item.name}</AutoOption>)}
                                ><Input placeholder={tips} readOnly={item.common.ReadOnly ? true : false} style={style} /></AutoComplete>)
                            }
                        </FormItem> :
                        <FormItem labelCol={{span:4}} wrapperCol={{span:24}} label={item.common.label}>
                            {
                                getFieldDecorator('actualValue', {
                                    initialValue: calculateTag?item.actualValue:this.calculate(item,this.props.list),
                                    rules:[
                                        {
                                            required: item.common.MustFill,
                                        }
                                    ]
                                })(<AutoComplete
                                    onFocus={this.handleFocus}
                                    onSearch={debounce(this.handleSearch, 800, this)}
                                    onChange={debounce(this.handleChange, 800, this)}
                                    optionLabelProp='value'
                                    disabled={!calculateTag}
                                    dataSource={useOptions.map((item, index) => <AutoOption key={index} value={item.value}>{item.name}</AutoOption>)}
                                ><TextArea placeholder={tips} readOnly={item.common.ReadOnly ? true : false} style={{...style,height:item.common.Rows*20}} /></AutoComplete>)
                            }
                        </FormItem>
                }
            </Form>

        )
    }
}
BaseInput = Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        props.handlePropChange(props.item, changedFields)
    }
})(BaseInput)
export default BaseInput
