import React, { Component, Fragment } from 'react'
import { withRouter } from 'react-router-dom'
import BaseText from '../components/BaseText'
import BaseInput from '../components/BaseInput'
import BaseTable from '../components/BaseTable'
import BaseSelect from '../components/BaseSelect'
import BaseRadio from '../components/BaseRadio'
import BaseCheckbox from '../components/BaseCheckbox'
import BaseDate from '../components/BaseDate'
import BaseUpload from '../components/BaseUpload'
import { message, Button, Card, Modal, Tooltip, Icon } from 'antd'
import HistoryList from './HistoryList';
import moment from 'moment'
import { debounce } from '../util'
import { getAction, postAction } from '@/axios';
import './index.less'
const accAdd=(arg1, arg2) => {
    var r1, r2, m;
    try {
        r1 = arg1.toString().split(".")[1].length;
    } catch(e) {
        r1 = 0;
    }
    try {
        r2 = arg2.toString().split(".")[1].length;
    }catch(e){
        r2 = 0;
    }
    m = Math.pow(10, Math.max(r1, r2));
    let sum = (arg1 * m + arg2 * m) / m
    return isNaN(sum)?'':sum;
}  

// 定义正则和规则的字典
const regexDict={
    '^[\\-0-9][0-9]*(.[0-9]+)?$':'数字',
    '^[\\u4e00-\\u9fa5]{0,}$':'汉字',
    '^\\w+([-+.]\\w+)*@\\w+([-.]\\w+)*\\.\\w+([-.]\\w+)*$':'邮箱',
    '^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\\d{8}$':'手机号码',
    '^\\d{15}|\\d{18}$':'身份证号',
    '^\\d{4}-\\d{1,2}-\\d{1,2}':'日期'
}
class Container extends Component {

    constructor(props) {
        super(props);
        this.state = {
            list: [],
            bindData: null,
            showHistory:false,
            enumerationData:null
        }

    }
    componentDidUpdate(preProps){
        if (JSON.stringify(preProps) !== JSON.stringify(this.props)) {
            this.getFormCompare(this.props.id).then(res=>{
                if(res === 'next'){
                    this.getFormData(this.props.id).then(res=>{
                        if(res === 'next'){
                            this.getEnumerationData(this.props.id).then(res=>{
                                if(res === 'next'){
                                    this.getBindData(this.props.id)
                                }
                            })
                        }
                    }) 
                }
            })        
        }
    }
    // 获取设计的表单信息
    getFormData = (id) => {
        return new Promise((resolve,reject)=>{
            getAction(`/ybt/form/task/info`, {
                formTaskId: id
            }).then(res => {
                if (res.success) {
                    let list=JSON.parse(res.obj.columnProperties) || [] 
                    this.setState({                        
                        list:list
                    })
                    //这里判断一下 里面有没有select 如果有才去请求下拉的枚举项
                    // console.log(list)
                    resolve('next')
                } else {
                    message.error(res.obj)
                }
            })
        })
        
    }
    /**
     *拿到枚举的下拉选项，需要接口
     *
     * @memberof BaseSelect
     */
    getEnumerationData=(id)=>{
        return new Promise((resolve,reject)=>{
            getAction(
                `/ybt/form/task/dropDown/dic/list?formTaskId=${id}`,
                {}
            ).then(res=>{
                if(res.success){
                    this.setState({
                        enumerationData:res.obj
                    })
                    resolve('next')  
                }else{
                    message.error(res.obj)
                }             
            })
        })
    }
    // 查询是否是第一次填报
    getFormCompare = (id) => {
        return new Promise((resolve,reject)=>{
            postAction(`/ybt/form/task/getAutoData?formTaskId=${id}`, {
                formTaskId: id
            }).then(res => {
                if (res.success && res.obj) {
                    let list=JSON.parse(res.obj) || [] 
                    this.setState({                        
                        list:list
                    })
                } else {
                    resolve('next')
                }
            })
        })
        
    }
    // 拿到各个组件绑定的数据
    getBindData = (id) => {
        let that = this
        getAction(`/ybt/form/task/table/data`, {
            formTaskId: id
        }).then(res => {
            if (res.success) {
                if (res.obj && JSON.stringify(res.obj) !== '{}') {
                    this.setState({
                        bindData: res.obj,
                        isFirst: false
                    }, () => {
                        this.fun_setData()
                        if (this.checkHasAutoFilled(this.state.list)) {
                            Modal.confirm({
                                title: '提醒',
                                content: '系统检测到自动填充数据有变动，是否覆盖？',
                                okText: '是',
                                cancelText: '否',
                                onOk: () => {
                                    that.getAutoInfo(id)
                                }
                            })
                        }
                    })
                } else {
                    this.getAutoInfo(id)
                }
            } else {
                message.error(res.obj)
            }
        })
    }

    /**
     *自动填充
     *
     * @memberof Container
     */
    getAutoInfo = (id) => {
        getAction(`/ybt/form/task/autofill/data`, {
            formTaskId: id
        }).then(res => {
            if (res.success) {
                this.setState({
                    bindData: res.obj
                }, () => {
                    this.fun_setAutoData()
                })
            }
        })
    }
    // 检测是否需要提醒自动
    checkHasAutoFilled = (list) => {
        let tag = false
        list.forEach(item => {
            if (item.children && item.children.length > 0) {
                // 检查表格列字段
                let tmp = item.children.findIndex(inner => inner.data.FillingMode === 'AutomaticFilling')
                if (tmp >= 0 && item.actualValue.length > 0) {
                    tag = true
                }
            } else {
                if (item.data && item.data.FillingMode === 'AutomaticFilling' && item.actualValue !== '') {
                    tag = true
                }
            }
        })
        return tag
    }

    /**
    |--------------------------------------------------
    | 处理跳转到下一步
    |--------------------------------------------------
    */
    handleNext = () => {
        let param = this.fun_getData()
        let tag=false
        try {
            tag=this.forbidSubmit()            
        } catch (error) {
            // console.log(error)
        }
        if(tag){
            param.columnFillValue=JSON.stringify(this.state.list)
            postAction(`/ybt/form/task/table/data/save?formTaskId=${this.props.id}`, param).then(res => {
                if (res.success) {
                    message.success('操作成功')
                    this.props.history.push(`/myFillingTask/step3?id=${this.props.id}`)
                } else {
                    message.error(res.obj)
                }
            })   
        }  
    }

    /**
     *
     *统一检查待提交的字段信息
     * @memberof Container
     */
    forbidSubmit = () => {
        // 第一步，检查必填项，必须逐项校验
        this.state.list.forEach(item=>{
            if(item.common.MustFill){
                let step_1=item
                if(step_1.actualValue === '' || !step_1.actualValue){
                    message.warning(<span><span style={{color:'red'}}>【{step_1.common.name}】</span>不能为空</span>)
                    throw new Error('fail') //抛出错误终止foreach
                }
            }
        })
        
        // 第二步，检查校验规则
        this.state.list.forEach(item=>{
            if(item.common.regex !== ''){
                let step_2=item,regex                
                if(step_2.common.regex === 'zdy'){
                    // 自定义正则
                    regex=step_2.common.zdyRegex
                }else{
                    // 内置正则
                    regex=step_2.common.regex
                }
                // 如果返回的值是空的，就不检查
                if(step_2.actualValue && step_2.actualValue !== ''){
                    if(!new RegExp(regex).test(step_2.actualValue)){
                        message.warning(<span><span style={{color:'red'}}>【{step_2.common.name}】</span>格式有误，{step_2.common.regex !=='zdy' && <span>应该填入{regexDict[regex]},</span>}请修改</span>)
                        throw new Error('fail')
                    }
                }
            }
        })
        
        return true
    }
    /**
    |--------------------------------------------------
    | 根据list取出后端需要的数据
    |--------------------------------------------------
    */
    fun_getData = () => {
        let data = {}
        let list=this.state.list.map(item=>{
            if(item.data && item.data.formula && item.data.formula !== ''){
                //有计算规则的需要计算
                item.actualValue= this.calculate(item,this.state.list)
            }else if(item.type==='table'){
                //筛选出日期类型的控件,并且格式化值
                let actualValue = JSON.parse(JSON.stringify(item.actualValue));
                item.children.forEach(tableChild=>{
                    if(tableChild.common.rowType === 'date'){
                        let onlyId = tableChild.common.onlyId;
                        actualValue = actualValue.map(values=>{
                            return {
                                ...values,
                                [onlyId]:values[onlyId]?moment(values[onlyId]).format('YYYY-MM-DD'):''
                            }
                        })
                    }
                })
                item.actualValue = actualValue;
            }
            return item
        })
        this.setState({
            list
        })
        list.forEach(item => {
            if (item.type !== 'text') {
                if (item.type === 'checkbox') {
                    if (item.actualValue !== '') {
                        data[item.common.onlyId] = item.actualValue.join(',')
                    } else {
                        data[item.common.onlyId] = item.actualValue
                    }
                }else if(item.type === 'table'){
                    //筛选出日期类型的控件,并且格式化值
                    let actualValue = JSON.parse(JSON.stringify(item.actualValue));
                    item.children.forEach(tableChild=>{
                        if(tableChild.common.rowType === 'date'){
                            let onlyId = tableChild.common.onlyId;
                            actualValue = actualValue.map(values=>{
                                return {
                                    ...values,
                                    [onlyId]:values[onlyId]?moment(values[onlyId]).format('YYYY-MM-DD'):''
                                }
                            })
                        }
                    })
                    data[item.common.onlyId] = actualValue
                } else {
                    data[item.common.onlyId] = item.actualValue                  
                }
            }
        })
        return data
    }
    calculate=(item,list)=>{
        if(item.data.formula && item.data.formula !== ''){
            let onlyId_list=item.data.formula.split('+')
            // 检查是否存在公式嵌套
            let total=onlyId_list.reduce((prev,current)=>{
                // 查找公式中配置的onlyid在不在list中
                // 区分两种情况，普通组件和table组件
                let number=0
                if(current.indexOf('.')>0){
                    // table
                    let tableId=current.split('.')[0],rowId=current.split('.')[1]
                    // 先找到table
                    let tableData=list.find(inner=>inner.common.onlyId === tableId)
                    if(tableData){
                        number=tableData.actualValue.reduce((prev,current)=>{
                            return accAdd(prev,Number(current[rowId]))
                        },0)
                    }
                }else{
                    // 其他组件
                    let target=list.find(inner=>inner.common.onlyId === current)
                    if(target){
                        number=Number(target.actualValue)
                    }
                }
                return accAdd(prev,number)
            },0)
            return total
        }
    }
    /**
     *刷入回显数据
     *
     * @memberof Container
     */
    fun_setData = () => {
        if (this.state.bindData) {
            let data = JSON.parse(JSON.stringify(this.state.bindData))
            let list = JSON.parse(JSON.stringify(this.state.list))
            list.forEach(item => {
                let tmp_key = item.common.onlyId
                if (data[tmp_key]) {

                    item.actualValue = data[tmp_key]
                }
            })
            
            // 刷入数据
            this.setState({
                list
            })
        }
    }
    /**
     *刷入自动填充的数据
     *
     * @memberof Container
     */
    fun_setAutoData=()=>{
        if (this.state.bindData) {
            let data = JSON.parse(JSON.stringify(this.state.bindData))
            let list = JSON.parse(JSON.stringify(this.state.list))
            list.forEach(item => {
                let tmp_key = item.common.onlyId
                if (data[tmp_key]) {
                    item.originValue = data[tmp_key]
                    item.actualValue = data[tmp_key]
                }
            })
            
            // 刷入数据
            this.setState({
                list
            })
        }

    }
    /**
     *表单更新的时候写入最新的数据，同时设置校验状态
     *
     * @memberof Container
     */
    handlePropChange = (item, changedFields) => {
        if (item.type === 'date') {
            changedFields['actualValue'] = moment(changedFields['actualValue']).format('YYYY-MM-DD')
        }
        // 更新输入框和其他组件的数据，不包括表格
        let tmpList = this.state.list.map(inner => {
            if (inner.id === item.id) {                
                inner.actualValue = changedFields.actualValue
            }
            return inner
        })
        this.setState({
            list: tmpList
        })
    }
    /**
    |--------------------------------------------------
    | 存储值字段方便后续请求使用
    |--------------------------------------------------
    */
    sendBindDataValue = (id, value, cellId) => {
        let index = this.state.list.findIndex(item => item.id === id)
        let tmp_list = JSON.parse(JSON.stringify(this.state.list))
        let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
        if (cellId) {
            tmp_item.children = tmp_item.children.map(item => {
                if (item.id === cellId) {
                    item.data.BindDataValue = value
                }
                return item
            })
        } else {
            tmp_item.data.BindDataValue = value
        }
        tmp_list.splice(index, 1, tmp_item)
        this.setState({
            list: tmp_list
        })
    }
    // 关联填充
    sendAssociationData = (data) => {
        if (JSON.stringify(data) !== '{}') {
            let tmp_list = JSON.parse(JSON.stringify(this.state.list))
            for (let k in data) {
                let index = tmp_list.findIndex(item => item.common.onlyId === k)
                if (index >= 0) {
                    tmp_list[index].originValue = data[k]
                    tmp_list[index].actualValue = data[k]
                }
            }
            
            this.setState({
                list: tmp_list
            })
        }
    }
    /**
    |--------------------------------------------------
    | 表格的单元格更新，行数以下划线后面的数字作为区分
    |--------------------------------------------------
    */
    handleEditTable = (allFields, id, changedFields) => {
        // 找到需要修改的组件的下标
        let index = this.state.list.findIndex(item => item.id === id), tmp_arr = []
        if (index >= 0) {
            for (let k in allFields) {
                let split_arr = k.split('##'), key = split_arr[0], arr_index = split_arr[1] - 1
                tmp_arr[arr_index] = { ...tmp_arr[arr_index], [key]: allFields[k] }
            }
            // 将填报的表格数据灌进list
            let tmp_list = JSON.parse(JSON.stringify(this.state.list))
            let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
            tmp_item.actualValue = tmp_arr
            // 删除原位置的对象插入新的对象
            tmp_list.splice(index, 1, tmp_item)
            this.setState({
                list: tmp_list
            })
        }
    }
    fillRow = (parentId, onlyId, value, data,colIndex) => {
        let index = this.state.list.findIndex(item => item.id === parentId)
        let tmp_list = JSON.parse(JSON.stringify(this.state.list))
        let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
        let editIndex= colIndex
        let newData = {}
        for (let k in data) {
            if (k && data[k]) {
                newData = data[k]
            }
        }
        if(value.indexOf(',') !== -1){
            newData[onlyId]=value.split(',')[1]
        } 
        if (editIndex >= 0) {
            let dataItem = {
                ...tmp_item.actualValue[editIndex],
                ...newData
            }
            let actualValue = JSON.parse(JSON.stringify(tmp_item.actualValue))
            actualValue.splice(editIndex, 1, dataItem)
            tmp_item.actualValue = actualValue
            tmp_list.splice(index, 1, tmp_item)
            this.setState({
                list: tmp_list
            })
        }
    }
    checkBindRow = (list, onlyId) => {
        return list.some(item => item.data && item.data.AssociatedControlsID === onlyId)
    }
    /**
    |--------------------------------------------------
    | 新增一个空白的行填数据
    |--------------------------------------------------
    */
    handleAddRow = (obj, id) => {
        let index = this.state.list.findIndex(item => item.id === id)
        let tmp_list = JSON.parse(JSON.stringify(this.state.list))
        let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
        if (!tmp_item.actualValue || tmp_item.actualValue === '') {
            tmp_item.actualValue = [obj]
        } else {
            tmp_item.actualValue.push(obj)
        }
        tmp_list.splice(index, 1, tmp_item)
        this.setState({
            list: tmp_list
        })
    }
    /**
    |--------------------------------------------------
    | 删除表格中的某一行
    |--------------------------------------------------
    */
    handleDeleteRow = (record, id) => {
        let index = this.state.list.findIndex(item => item.id === id)
        let tmp_list = JSON.parse(JSON.stringify(this.state.list))
        let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
        tmp_item.actualValue.splice(record.key - 1, 1)
        tmp_list.splice(index, 1, tmp_item)
        this.setState({
            list: tmp_list
        })
    }

    /**
    |--------------------------------------------------
    | 更新各个组件的下拉选项
    |--------------------------------------------------
    */
    handleUpdateOption = (data, componentId, type, cellId) => {
        let index = this.state.list.findIndex(item => item.id === componentId)
        let tmp_list = JSON.parse(JSON.stringify(this.state.list))
        let tmp_item = JSON.parse(JSON.stringify(tmp_list[index]))
        if (cellId) {
            // 表格模式
            tmp_item.children = tmp_item.children.map(item => {
                if (item.id === cellId) {
                    item.common[type] = data
                }
                return item
            })
        } else {
            // 其他模式
            tmp_item.common[type] = data
        }
        tmp_list.splice(index, 1, tmp_item)
        this.setState({
            list: tmp_list
        })
    }
    handleComponent = (item) => {
        switch (item.type) {
            case 'text':
                return  <BaseText
                            item={item}
                        />
            case 'input':
                return <BaseInput
                            sendAssociationData={this.sendAssociationData}
                            sendBindDataValue={this.sendBindDataValue}
                            list={this.state.list}
                            id={this.props.id}
                            handleUpdateOption={this.handleUpdateOption}
                            handlePropChange={this.handlePropChange}
                            item={item} 
                        />
            case 'table':
                return <BaseTable
                            id={this.props.id}
                            handleUpdateOption={this.handleUpdateOption}
                            handleDeleteRow={this.handleDeleteRow}
                            handleAddRow={this.handleAddRow}
                            handleEditTable={debounce(this.handleEditTable, 800, this)}
                            item={item}
                            sendBindDataValue={this.sendBindDataValue}
                            list={this.state.list}
                            fillRow={this.fillRow}
                            enumerationData={this.state.enumerationData}
                        />
            case 'dropDown':
                return <BaseSelect 
                            sendAssociationData={this.sendAssociationData}
                            id={this.props.id} 
                            handlePropChange={this.handlePropChange} 
                            list={this.state.list}
                            item={item} 
                            enumerationData={this.state.enumerationData}
                            handleUpdateOption={this.handleUpdateOption}
                        />
            case 'radio':
                return <BaseRadio 
                            id={this.props.id} 
                            handlePropChange={this.handlePropChange} 
                            item={item} 
                        />
            case 'checkbox':
                return <BaseCheckbox 
                            id={this.props.id} 
                            handlePropChange={this.handlePropChange} 
                            item={item} 
                        />
            case 'date':
                return <BaseDate
                            sendBindDataValue={this.sendBindDataValue}
                            list={this.state.list}
                            id={this.props.id}
                            handlePropChange={this.handlePropChange}
                            handleUpdateOption={this.handleUpdateOption}
                            item={item} 
                        />
            case 'upload':
                return <BaseUpload 
                            item={item} 
                        />
            default:
                return null
        }
    }
    /**
     *将layout转化为css样式，采用浮动布局解决填报页面的bug     *
     * @memberof Container
     */
    transformAxisToStyle=(inner)=>{
        let layout=inner.layout
        return {
            width:layout.w/24*960,
            height:inner.type !== 'table'?layout.h*40:'auto',
            float:'left',
            marginBottom:10,
            paddingLeft:10,
            marginLeft:(layout.left || 0)/24*960
        }
    }
    /**
     *
     *对组件list按照layout的属性进行顺序调整
     * @memberof Container
     */
    sortByLayout = (list) => {
        let deepCopyList = JSON.parse(JSON.stringify(list))
        deepCopyList = deepCopyList.map(item => {
            item.layout.plus = item.layout.x + item.layout.y * 10000
            return item
        })
        deepCopyList = deepCopyList.sort((a, b) => a.layout.plus - b.layout.plus)
        return deepCopyList
    }
    /**
     *将组件分组
     *
     * @memberof Container
     */
    divide=(list)=>{
        // 为了后续计算，需要先在这里算出当前组件与前面组件的相隔栅格数，方便计算marginLeft
        let arr=[[list[0]]]
        for(let i=1;i<list.length;i++){ 
            let prevOne=list[i-1]
            if(list[i].layout.y === prevOne.layout.y){
                let arrLast=arr[arr.length-1],item=list[i]
                if(arrLast[arrLast.length-1]){
                    item.layout.left=item.layout.x-arrLast[arrLast.length-1].layout.x-arrLast[arrLast.length-1].layout.w
                }else{
                    item.layout.left=item.layout.x
                }
                arr[arr.length-1].push(item)
            }else{
                if(list[i].layout.y+list[i].layout.h<prevOne.layout.y+prevOne.layout.h){                    
                    let arrLast=arr[arr.length-1],item=list[i]
                    if(arrLast[arrLast.length-1]){
                        item.layout.left=item.layout.x-arrLast[arrLast.length-1].layout.x-arrLast[arrLast.length-1].layout.w
                    }else{
                        item.layout.left=item.layout.x
                    }
                    arr[arr.length-1].push(item)
                }else{
                    // 这里还需要判断不连续但是浮动在后面的情况,<=
                    let arrLast=arr[arr.length-1],item=list[i]
                    let has=arrLast.find(inner=>item.layout.y+item.layout.h<=inner.layout.y+inner.layout.h)
                    if(has){
                        item.layout.left=item.layout.x-has.layout.x-has.layout.w
                        arr[arr.length-1].push(item)
                    }else{
                        item.layout.left=item.layout.x
                        arr.push([item])    
                    }             
                }
            }
        }
        return arr
    }
    //显示历史数据填充弹窗
    _showHistory = () => {
        this.setState({
            showHistory:true
        })
    }
    //关闭历史数据列表弹窗
    _closeHistory = () => {
        this.setState({showHistory:false})
    }
    //选择历史数据确认 - 根据formtaskid获取历史填报数据
    _getHistoryData = (formTaskId) => {
        postAction(`/ybt/form/task/getHisData?formTaskId=${formTaskId}`).then(res=> {
            if(res.success){
                this.setState({showHistory:false});
                let list = this.state.list.map(item=>{
                    let id = item.common.onlyId;//唯一标识
                    if(res.obj[id]===0 || res.obj[id] ){
                        return {...item,actualValue:res.obj[id]}
                    }else{
                        return item;
                    }
                });
                this.setState({list})
            }else{
               message.error(res.obj);
            }
        })
    }
    render() {
        let list = this.sortByLayout(this.state.list)
        let a=this.divide(list)
        return (
            <Fragment>
                <Card title={this.props.pageType === 'fill' ? '我要填报' : '预览'} extra={<Tooltip placement="left" title='完成一个填报流程后，可以在此选择历史填报数据'><Button onClick={this._showHistory}>历史数据填充<Icon style={{color:'#24695C'}} type="exclamation-circle" /></Button></Tooltip>}>
                    <div className='sp-view' style={{ overflowY: 'auto',overflowX:'hidden' }}>
                        {
                            a.map((item, index) => {
                                return (
                                    <div key={index} style={{height:'auto',overflow:'hidden'}}>
                                        {
                                            item.map((inner,i)=>{
                                                if(inner){
                                                    return(
                                                        <div  style={this.transformAxisToStyle(inner)} key={`${inner.id}`}>
                                                            <div>
                                                                {this.handleComponent(inner)}
                                                            </div>
                                                        </div>
                                                    )
                                                }else{
                                                    return null
                                                }
                                                
                                            })
                                        }
                                    </div>
                                    
                                )
                            })
                        }
                    </div>
                </Card>
                {
                    this.props.pageType === 'fill' ? <div style={{ marginTop: 10, textAlign: 'right' }}>
                        <Button onClick={this.handleNext} type='primary' style={{ marginRight: 8 }}>下一步</Button>
                        <Button onClick={() => { this.props.history.push(`/myFillingTask`) }} style={{ marginRight: 40 }}>取消</Button>
                    </div> : null
                }
                <Modal 
                    visible={this.state.showHistory}
                    title='选择历史数据'
                    onCancel={this._closeHistory}
                    footer={false}
                    destroyOnClose={true}
                    bodyStyle={{padding:0}}
                >
                    <HistoryList onOk={this._getHistoryData} onCancel={this._closeHistory}/>
                </Modal>
            </Fragment>

        )
    }
}


export default withRouter(Container)