import React, { Component } from 'react';
import {Button, message} from 'antd';
import {postAction} from '@/axios';

export default class HistoryList extends Component {
    constructor(props){
        super(props);
        this.state={
            list:[],
            selectedHistory:""
        }
    }
    componentDidMount(){
        let formId = window.localStorage.getItem("formIdStep2");
        postAction("/ybt/form/task/getFormTaskHis?pageNum=1&pageSize=0",{formId}).then(res=>{
            if(res.success){
                this.setState({list:res.obj.rows || []})
            }else{
                message.error(res.obj)
            }
        })
    }
    //确定
    _ok = () => {
        this.props.onOk(this.state.selectedHistory)
    }
    //取消
    _cancle = () => {
        this.props.onCancel();
    }
    render() {
        const activeStyle={padding:'8px 10px 8px 30px',borderBottom:'1px solid #eee',cursor:'pointer',background:"#16a8f8"};
        const normalStyle={padding:'8px 10px 8px 30px',borderBottom:'1px solid #eee',cursor:'pointer'}
        return (
            <React.Fragment>
                <div style={{height:'400px',overflow:'auto'}}>   
                    {
                        this.state.list.map((item,index)=>{
                            return (
                                <div 
                                    style={item.formTaskId===this.state.selectedHistory?activeStyle:normalStyle} 
                                    key={item.formTaskId} 
                                    onClick={()=>{this.setState({selectedHistory:item.formTaskId})}}
                                >
                                    <span style={{paddingRight:'10px'}}>{index+1},</span>{item.taskEndTime}
                                </div>
                            )
                        })
                    }
                </div>
                <div style={{textAlign:'center',padding:'10px 0px',borderTop:'1px solid #eee'}}>
                    <Button style={{marginRight:'20px'}} type='primary' onClick={this._ok}>确 定</Button>
                    <Button onClick={this._cancle}>取 消</Button>
                </div>
            </React.Fragment>
        )
    }
}
