import React, { Component } from 'react'
import {Form,Radio} from 'antd'
import {generateStyle} from '../util'
const FormItem=Form.Item
class BaseRadio extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        let item=this.props.item
        const formItemLayout = {
            labelCol: {
              span:4
            },
            wrapperCol: {
              span:20
            },
        };
        let style=generateStyle(this.props.item.style) || {}
        let options=this.props.item.common.options.split(/,|，/)
        options=options.map(item=>{
            if(item && item !== ''){
                return {
                    name:item,
                    value:item
                }
            }else{
                return  null
            }
        }).filter(item=>item)
        return (
            <Form {...formItemLayout}>
                <FormItem label={item.common.label}>
                    {
                        getFieldDecorator('actualValue',{
                            initialValue: item.actualValue,
                        })(<Radio.Group  style={style}>
                            {
                                options.map((item,index)=><Radio value={item.value} key={index}>{item.name}</Radio>)
                            }                           
                          </Radio.Group>)
                    }
                </FormItem>
            </Form>
        )
    }
}
BaseRadio=Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        props.handlePropChange(props.item,changedFields)
    }
})(BaseRadio)
export default BaseRadio
