import React, { Component } from 'react'
import {Form,Select} from 'antd'
import {generateStyle} from '../util'
import { postAction } from '@/axios';
const FormItem=Form.Item,Option=Select.Option
class BaseSelect extends Component {
    componentDidMount(){
        this.handleFocus();
    }
    componentDidUpdate(preProps){
        if(preProps.item.actualValue!==this.props.item.actualValue){
            let hasAss=this.fun_checkAssociattion(this.props.item.common.onlyId)
            if(hasAss){
                this.getBindAssociation(this.props.actualValue)
            }
        }
    }
    
    handleSelectChange=(value)=>{
        let hasAss=this.fun_checkAssociattion(this.props.item.common.onlyId)
        if(hasAss){
            this.getBindAssociation(value)
        }
    }
    // 得到关联填充的数据
    getBindAssociation=(value)=>{
        let params
        if(this.props.item.data.FillingMode === 'AssociationFilling' || this.props.item.data.FillingMode === 'AutomaticFilling'){
            params=this.fun_combineParam(this.props.item.common['ass_params'] || [])
        }else{
            // 静态数据就直接取值
            params={
                [this.props.item.common.onlyId]:value
            }
        }
        postAction(`/ybt/form/task/associationFill/data?formTaskId=${this.props.id}&tableWidgetId=&widgetId=${this.props.item.common.onlyId}`, params).then(res => {
            if (res.success) {
                // 将取到的数据推送到父级的list
                this.props.sendAssociationData(res.obj)
                if(res.obj){
                    // 递归调用链式关联
                    this.chainAssociattion(res.obj)                    
                }
            }
        })
    }
    handleFocus = () => {
        // 检查关联
        let hasAss=this.fun_checkAssociattion(this.props.item.common.onlyId)
        if(hasAss){
            let param = {
                tableWidgetId: '',
                widgetId: this.props.item.common.onlyId,
                formId: this.props.id,
                fillingMode: 'AssociationFilling'
            }
            postAction(`/ybt/form/preview/need/widget/dataParams`, param, 1, 1).then(res => {
                if (res.success) {
                    if (JSON.stringify(res.obj) !== '{}' && JSON.stringify(res.obj) !== '[]') {
                        this.props.handleUpdateOption(res.obj,this.props.item.id,'ass_params')                        
                    }
                }
            })
        }
    }
    /**
     *根据params拿到想要的参数     *
     * @memberof BaseInput
     */
    fun_combineParam=(params)=>{
        let list=this.props.list,obj={}
        params.forEach(item=>{
            if(item.indexOf('.')>0){
                // 参数关联的是表格列或者是值字段
                if(item.indexOf('BindDataValue')>=0){
                    let index = list.findIndex(inner => inner.common.onlyId ===  item.split('.')[0])
                    if (index >= 0) {
                        obj[item] = list[index].data.BindDataValue || ''
                    }
                }
            }else{
                let index=list.findIndex(inner=>inner.common.onlyId === item)
                if(index>=0){
                    obj[item]=list[index].actualValue
                }                
            }
        })
        return obj
    }
    /**
     *递归调用关联填充的全部数据并回显
     *
     * @memberof BaseInput
     */
    chainAssociattion=(data)=>{
        for(let k in data){
            if(k){
                let isAss=this.fun_checkAssociattion(k)
                if(isAss){
                    let param = {
                        tableWidgetId: '',
                        widgetId: k,
                        formId: this.props.id,
                        fillingMode: 'AssociationFilling'
                    }
                    postAction(`/ybt/form/preview/need/widget/dataParams`, param, 1, 1).then(res => {
                        if (res.success) {
                            
                            let params=this.fun_combineParam(res.obj)
                            postAction(`/ybt/form/preview/associationFill/data?formId=${this.props.id}&tableWidgetId=&widgetId=${k}`, params).then(res => {
                                if (res.success) {
                                    
                                    this.props.sendAssociationData(res.obj)
                                    if(res.obj){
                                        this.chainAssociattion(res.obj)                   
                                    }
                                }
                            })                            
                        }
                    })
                                                        
                }
            }
        }
    }
    /**
     *检测是否有其他任意组件和当前组件关联,首先要过滤掉自身
     *
     * @memberof BaseInput
     */
    fun_checkAssociattion=(onlyId)=>{
        let tag=false,list=this.props.list
        list=list.filter(item=>item.common.onlyId !== onlyId)
        list.forEach(item=>{
            if(item.children){
                if(item.children.length>0){
                    let index=item.children.findIndex(i=>onlyId === i.data.AssociatedControlsID)
                    if(index>=0){
                        tag=true
                    }
                }
            }
            if(item.data && onlyId === item.data.AssociatedControlsID){
                tag=true
            }
        })
        return tag
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        let item=this.props.item
        const formItemLayout = {
            labelCol: {
              span:4
            },
            wrapperCol: {
              span:20
            },
        };
        let style=generateStyle(this.props.item.style) || {}
        let options=[]
        if(this.props.enumerationData && this.props.enumerationData[this.props.item.common.onlyId]){
            options=this.props.enumerationData[this.props.item.common.onlyId]
        }else{
            options=this.props.item.common.options.split(/,|，/)
            options=options.map(item=>{
                if(item && item !== ''){
                    return {
                        key:item,
                        value:item
                    }
                }else{
                    return  null
                }
            }).filter(item=>item)
        }
        return (
            <Form {...formItemLayout}>
                <FormItem label={item.common.label}>
                    {
                        getFieldDecorator('actualValue',{
                            initialValue: item.actualValue,
                        })(<Select 
                            onChange={this.handleSelectChange}  
                            style={style}  
                            filterOption={(input, option) =>option.props.children.indexOf(input) >= 0}
                            showSearch
                            optionFilterProp="children"
                            onFocus={this.handleFocus}
                        >
                            {
                                options.map((item,index)=><Option value={item.value} key={index}>{item.key}</Option>)
                            }
                        </Select>)
                    }
                </FormItem>
            </Form>
        )
    }
}
BaseSelect=Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        props.handlePropChange(props.item,changedFields)
    }
})(BaseSelect)
export default BaseSelect
