import React, { Component } from 'react'
import { debounce } from '../util'
import { postAction } from '@/axios';
import {Modal,Input,Row,Col,Button,Form,AutoComplete} from 'antd'
const {TextArea}=Input,FormItem=Form.Item,AutoOption = AutoComplete.Option
class MyModal extends Component {
    state={
        value:this.props.value,
        options:[]        
    }    
    
    fun_combineParam = (current,value) => {
        let params = this.props.params || []
        let list = this.props.list, obj = {}
        params.forEach(item => {
            if (item.indexOf('.') > 0) {
                // 参数关联的是表格列或者是值字段
                if(item.indexOf('BindDataValue')>=0){
                    // 存在关联某联想控件的情况
                    let beforeStr=item.replace('.BindDataValue','')
                    if(beforeStr.indexOf('.')>0){
                        // 表格列
                        let tableId=beforeStr.split('.')[0],childId=beforeStr.split('.')[1]
                        let tableItem=list.find(inner=>inner.common.onlyId === tableId)
                        if(tableItem){
                            let childIndex=tableItem.children.findIndex(i=>i.common.onlyId === childId)
                            if(childIndex>=0){
                                if(this.state.BindDataValue){
                                    obj[item]=this.state.BindDataValue[tableId][childId]
                                }else{
                                    obj[item]=''
                                }
                            }
                        }
                    }else{
                        // 其他
                        let index = list.findIndex(inner => inner.common.onlyId === beforeStr)
                        if (index >= 0) {
                            obj[item] = list[index].data.BindDataValue || ''
                        }
                    }
                }else{
                    let tableId=item.split('.')[0],childId=item.split('.')[1]
                    let tableItem=list.find(inner=>inner.common.onlyId === tableId)
                    if(tableItem){
                        let childIndex=tableItem.children.findIndex(i=>i.common.onlyId === childId)
                        if(childIndex>=0){
                            obj[item]=value
                        }
                    }
                }
            } else {
                let index = list.findIndex(inner => inner.common.onlyId === item)
                if (index >= 0) {
                    obj[item] = list[index].actualValue
                }
            }
        })
        return obj
    }
    handleSearch = (item, value) => {
        let type = item.data.FillingMode
        if (type === 'Manual') {
            // this.getHistory(item,value)
        } else if (type === 'Association') {
            this.getAssociation(item,value)
        }
    }
    handleChange = (item,value) => {        
        if(item.data.FillingMode === 'Association'){
            this.props.form.setFieldsValue({
                "value":value.split(',')[1]
            })
        }        
    }
    getAssociation = (item,value) => {
        if(value !== ''){
            let params = this.fun_combineParam(item,'params',value)
            let param = {
                "condition": {
                    "tableWidgetId": this.props.item.common.onlyId,
                    "widgetId": item.common.onlyId,
                    "widgetValue": value
                },
                "params": params
            }
            postAction(`/ybt/form/task/association/data?formTaskId=${this.props.id}`, param).then(res => {
                if (res.success) {
                    this.setState({
                        options:res.obj
                    })
                }
            })
        }        
    }
    handleSave=()=>{
        let value=this.props.form.getFieldValue('value')
        this.props.handleSaveShuju(value)
    }
    render() {
        const {getFieldDecorator}=this.props.form
        let item=this.props.childitem
        let associationOptions = this.state.options || []   
        let historyOptions=this.state.historyOptions || []
        let useOptions = []
        if (item.data.FillingMode === 'Manual') {
            useOptions = historyOptions.map(inner => {
                let newObj = {}
                // 过滤掉时间戳
                if (inner.submitTime) {
                    delete inner.submitTime
                }
                // 遍历item
                for (let k in inner) {
                    if (k && inner[k] && inner[k] !== '') {
                        newObj.name = inner[k]
                        newObj.value = inner[k]
                    }
                }
                return newObj
            })
        } else if (item.data.FillingMode === 'Association') {
            // 联想模式
            useOptions = associationOptions.map(inner => {
                let newObj = {}, str = []
                newObj.value = (inner[item.data.BindDataField]||'')+','+inner[item.data.DataValueField] || ''
                for (let key in inner) {
                    str.push(inner[key])
                }
                newObj.name = str.join('，')
                return newObj
            })
        }
            // 过滤options防止报错
        useOptions = useOptions.filter(item => item.name && item.name !== '')
        return (
            <Modal
                visible={this.props.visible}
                title=''                
                footer={null}
                onCancel={this.props.handleClose}
                width={800}
            >
                <Row>                    
                    <Col span={24} style={{height:400,overflowY:'auto',marginTop:30,overflowX:'hidden'}}>
                        <Form>
                            <FormItem>
                                {
                                    getFieldDecorator('value',{
                                        initialValue:this.props.value || ''
                                    })(
                                        <AutoComplete                                            
                                            onSearch={debounce(this.handleSearch.bind(this,this.props.childitem),800,this)}
                                            optionLabelProp='value'
                                            onChange={debounce(this.handleChange.bind(this,this.props.childitem),800,this)}
                                            dataSource={useOptions.map((item, index) => <AutoOption key={index} value={item.value}>{item.name}</AutoOption>)}
                                        >
                                            <TextArea style={{height:160}}/>
                                        </AutoComplete>
                                    )
                                }
                                
                            </FormItem>
                        </Form>
                        
                    </Col>
                </Row>
                <div style={{textAlign:'center',padding:'30px 0 0 0'}}>                    
                    <Button onClick={this.handleSave} disabled={this.state.tag} type='primary' style={{marginRight:15}}>
                        确定
                    </Button>
                    <Button onClick={this.props.handleClose}>
                        取消
                    </Button>
                </div>
            </Modal>
        )
    }
}

export default Form.create()(MyModal)