import React, { Component } from 'react'
import Container from './container'
import {parseUrlSearch} from '../util'
export default class index extends Component {
    state={
        type:'',
        id:''
    }
    componentDidMount() {
        let param=parseUrlSearch()
        this.setState({
            type:param.type,
            id:param.id || ''
        })
    }
    
    render() {
        return (
            <div style={{padding:20}} className='form-designer'>
                <Container id={this.state.id} pageType={this.state.type}/>
            </div>
        )
    }
}
